import React, { Component } from 'react';
import {
  Accordion,
  AccordionItem,
  AccordionItemTitle,
  AccordionItemBody
} from 'react-accessible-accordion';
import Parallax from 'react-rellax';

import styles from './styles.module.css';
import './accordion.css';

import imageSoekarnoOrasi from './images/soekarno-orasi.png';

class FAQSection extends Component {
  questions = [
    {
      question: 'Bagaimana cara kalian menjaga ketidakberpihakan',
      answer:
        'Tim SuaraKami tidak berafiliasi dengan partai manapun dan diakui oleh KPU sebagai aplikasi yang sah digunakan untuk pemilihan umum. Pendanaan kami pun transparan dan dapat diakses publik, tanpa ada kontraprestasi yang berhubungan dengan partai politik tertentu'
    },
    {
      question: 'Darimana sumber pendanaan SuaraKami',
      answer:
        'Aplikasi SuaraKami memperoleh dana dari badan independen, seperti KPU dan organisasi non-partai lainnya. Namun, kamu dapat membantu keber dengan cara membeli merchandise SuaraKami yang tersedia di website.'
    },
    {
      question: 'Kapan SuaraKami bisa diakses',
      answer:
        'Mulai bulan Februari 2019, SuaraKami dapat diakses melalui website suarakami.id.'
    },
    {
      question: 'Siapa saja dibalik SuaraKami',
      answer:
        'Suarakami diinisiasi dan dikembangkan murni oleh tujuh mahasiswa Indonesia yang berkuliah di Jerman dan berkeinginan untuk meningkatkan partisipasi mahasiswa Indonesia dalam pemilihan umum dengan cara yang inovatif dan cocok dengan karakteristik generasi muda.'
    },
    {
      question: 'Bagaimana cara pernyataan-pernyataan disusun dan dikembangkan',
      answer:
        'Pernyataan yang disajikan di aplikasi SuaraKami merupakan kumpulan isu penting dan aktual di dalam negeri. Isu-isu tersebut dikritisi oleh berbagai elemen masyarakat, badan, hingga partai politik yang bersangkutan. Pernyataan tersebut dirangkai dalam algoritma yang mengunakan sistem pembobotan untuk menemukan kesesuaian antara jawaban pemilih dengan karakteristik partai politik yang ada.'
    },
    {
      question: 'Bagaimana untuk dapat informasi lebih lanjut soal SuaraKami',
      answer:
        'Untuk organisasi atau badan yang tertarik dalam pengkajian pernyataan yang disajikan diaplikasi SuaraKami, media yang ingin menjalin kerjasama atau teman-teman yang ingin berpartisipasi dalam penyempurnaan aplikasi SuarKami, hingga hanya ingin bertanya-tanya , dapat menghubungi tim SuaraKami di laman kontak kami.'
    }
  ];

  renderQuestions() {
    return this.questions.map((question, index) => (
      <div className="question-container" key={index}>
        <div className="question">{question.question}</div>
        <div className="answer">{question.answer}</div>
      </div>
    ));
  }

  renderAccordion() {
    const renderQuestions = () =>
      this.questions.map((question, index) => (
        <AccordionItem className="question-container" key={index}>
          <AccordionItemTitle className="question accordion__title accordion__title--animated">
            <h3 className="u-position-relative">
              <span>{question.question}</span>
              <div className="accordion__arrow" role="presentation" />
            </h3>
          </AccordionItemTitle>
          <AccordionItemBody className="answer accordion__body">
            <p>
              <span>{question.answer}</span>
            </p>
          </AccordionItemBody>
        </AccordionItem>
      ));

    return <Accordion>{renderQuestions()}</Accordion>;
  }

  render() {
    return (
      <div className={styles.outerContainer}>
        <div className={['wrapper flex', styles.wrapper].join(' ')}>
          <div className={styles.title}>
            <h1 className={styles.h1}>FAQ</h1>
          </div>
          <div className="questions">{this.renderAccordion()}</div>
        </div>
        <Parallax zIndex={-1} speed={-2} percentage={-1.7}>
          <img
            className={styles.soekarnoOrasi}
            src={imageSoekarnoOrasi}
            alt="ir-soekarno-orasi"
          />
        </Parallax>
      </div>
    );
  }
}

export default FAQSection;
