import React from 'react';
import { Keyframes, Trail, config } from 'react-spring';
import { PaperCard } from '../paper-card';
import { getRandomInt } from '../../../../../../utils/getRandomInt';

import styles from './styles.module.css';

import imgPartyA from './images/party-a.svg';
import imgPartyB from './images/party-b.svg';
import imgPartyC from './images/party-c.svg';
import imgPartyD from './images/party-d.svg';
import imgPartyE from './images/party-e.svg';

const Container = Keyframes.Spring({
  initial: {
    transform: 'rotateX(0deg)',
    opacity: 1,
    from: {
      transform: 'rotateX(180deg)',
      opacity: 0
    },
    config: config.wobbly
  },
  onVote: async (next, cancel, props) => {
    await next({
      transform: 'rotateX(-180deg)',
      opacity: 0,
      config: config.gentle
    });
    if (props.onFinished) {
      props.onFinished();
    }
  }
});

export class PartyList extends React.Component {
  /* Variable definitions */
  parties = [
    {
      key: 1,
      name: 'Partai Bumi Hijau',
      score: getRandomInt(27, 30),
      image: imgPartyA
    },
    {
      key: 2,
      name: 'Partai Cuma Janji',
      score: getRandomInt(23, 26),
      image: imgPartyB
    },
    {
      key: 3,
      name: 'Partai Bersinar',
      score: getRandomInt(18, 20),
      image: imgPartyC
    },
    {
      key: 4,
      name: 'Partai Lentera',
      score: getRandomInt(16, 18),
      image: imgPartyD
    },
    {
      key: 5,
      name: 'Partai Ga Jelas',
      score: getRandomInt(1, 6),
      image: imgPartyE
    }
  ];

  /* Function definitions */
  cbInitiaRenderingFinished = () => {
    console.log('yay');
  };

  render() {
    return (
      <div className={styles.container}>
        <Container state="initial">
          {props => (
            <h1 className={styles.header} style={props}>
              Kandidat kamu
            </h1>
          )}
        </Container>
        <Container state="initial" onFinished={this.cbOnVote}>
          {props => (
            <div style={props}>
              <Trail
                items={this.parties}
                keys={item => item.key}
                from={{
                  transform: 'rotateX(180deg)',
                  opacity: 0
                }}
                to={{
                  transform: 'rotateX(0deg)',
                  opacity: 1
                }}
              >
                {party => props => (
                  <div className={styles.listItemContainer} style={props}>
                    <PaperCard className={styles.listItem}>
                      <div className={styles.logoContainer}>
                        <img src={party.image} alt={party.name} />
                      </div>
                      <div className={styles.textContainer}>
                        <div className={styles.partyName}>{party.name}</div>
                        <div className={styles.voteScoreText}>
                          <span className={styles.voteScore}>
                            {party.score}
                          </span>{' '}
                          suara sama
                        </div>
                      </div>
                    </PaperCard>
                  </div>
                )}
              </Trail>
            </div>
          )}
        </Container>
      </div>
    );
  }
}
