import React from 'react';
import styles from './styles.module.css';

export class PaperCard extends React.Component {
  render() {
    return (
      <div
        className={[
          styles.paper,
          styles.paper_curl_right,
          this.props.className || ''
        ].join(' ')}
      >
        {this.props.children}
      </div>
    );
  }
}
