import React from 'react';
import { Ballot } from './components/ballot';
import { PartyList } from './components/party-list';
import { ConversationRenderer } from './components/conversation-renderer';
import { Scrollbars } from 'react-custom-scrollbars';
import styles from './styles.module.css';

export class Demo extends React.Component {
  state = {
    step: 'initial'
  };

  getWidget = () => {
    switch (this.state.step) {
      case 'voted':
        return {
          component: PartyList,
          props: {}
        };
      case 'pre-calculation':
        return {
          component: ConversationRenderer,
          props: {
            text: [
              'Setelah kamu memberikan suaramu di tema-tema yang ditampilkan',
              'Kamu akan diberikan bla bla'
            ],
            onFinished: () => this.setStep('voted')
          }
        };
      default:
        return {
          component: Ballot,
          props: {}
        };
    }
  };

  setStep = async step => {
    await this.setState({
      step
    });
  };

  render() {
    const Widget = this.getWidget();
    return (
      <div className={styles.viewContainer}>
        <Scrollbars>
          <div className={styles.scrollContainer}>
            <Widget.component
              step={this.state.step}
              setStepFn={this.setStep}
              {...Widget.props}
            />
          </div>{' '}
        </Scrollbars>
      </div>
    );
  }
}
