import React from 'react';
import { Transition, config } from 'react-spring';
import { sleep } from '../../../../../../utils/sleep';
import styles from './styles.module.css';

export class ConversationRenderer extends React.Component {
  constructor(props) {
    super(props);

    if (this.props.text == null || this.props.text.length === 0) {
      throw new Error('No text specified');
    }

    this.state = {
      currentTextIndex: 0,
      currentText: null,
      delay: this.props.delay || 2000
    };
  }

  /* Function definitions */
  componentDidMount() {
    this.startConversation();
  }

  startConversation = async (currentTextIndex = 0) => {
    if (currentTextIndex < this.props.text.length) {
      await this.setState({
        currentTextIndex,
        currentText: this.props.text[currentTextIndex]
      });
      await sleep(this.state.delay);
      this.startConversation(currentTextIndex + 1);
    } else {
      this.cbOnFinished();
    }
  };

  renderText = () => {
    if (this.state.currentTextIndex > this.props.text.length - 1) {
      return null;
    }
    return this.props.text[this.state.currentTextIndex];
  };

  cbOnFinished = () => {
    if (this.props.onFinished) {
      this.props.onFinished();
    }
  };

  render() {
    const currentText = this.state.currentText;
    return (
      <div className={styles.container}>
        <Transition
          items={currentText}
          from={{
            transform: 'rotateX(180deg)',
            opacity: 0
          }}
          enter={{
            transform: 'rotateX(0deg)',
            opacity: 1
          }}
          leave={{
            transform: 'rotateX(180deg)',
            opacity: 0
          }}
          config={config.gentle}
        >
          {currentText => props => (
            <div className={styles.textContainer}>
              <div style={props} className={styles.text}>
                {currentText}
              </div>
            </div>
          )}
        </Transition>
      </div>
    );
  }
}
